import { Component, OnInit, Injectable, ViewChild } from '@angular/core';
import { SwiperComponent, SwiperDirective, SwiperConfigInterface,
  SwiperScrollbarInterface, SwiperPaginationInterface } from 'ngx-swiper-wrapper';
// import { NgForm } from '@angular/forms';
import { Router, Resolve, RouterStateSnapshot,
  ActivatedRouteSnapshot } from '@angular/router';

@Component({
  selector: 'app-offers',
  templateUrl: './offers.component.html',
  styleUrls: ['./offers.component.scss']
})

@Injectable()
export class OffersComponent implements OnInit {

	constructor(private router: Router) {}

	// slider config
	index = 0;
	public config: SwiperConfigInterface = {
		direction: 'horizontal',
		slidesPerView: 3,
		spaceBetween: 30,
		keyboard: true,
		breakpoints:{
			768: {
			  slidesPerView: 1,
			},
			992: {
			  slidesPerView: 2,
			},
		}
		// navigation: true,
	};

	@ViewChild(SwiperComponent) componentRef: SwiperComponent;
	@ViewChild(SwiperDirective) directiveRef: SwiperDirective;

	
	public nextSlide(speed: number): void{
		this.componentRef.directiveRef.nextSlide(speed);
	}
	public prevSlide(speed: number): void{
		this.componentRef.directiveRef.prevSlide(speed);
	}

	// slider content
	sliderItems = [
		{ 
			'rout':      'package-USA',
			'color':     'blue',
			'title':     '8GB',
			'sub-title': ' ',
			'price':     '189',
			'days':      '30 ',
			'info'     : ' At&t         ',
			'name':      '    ',
			'options': [
				'      ',
				'    ',
				'   !  ',
			],
			'desc'     : '      .            ',
		},
		{ 
			'rout':      'returning-customer',
			'color':     'orange',
			'title':     'UNLIMITED',
			'sub-title': ' ',
			'price':     '29',
			'days':      '',
			'info': '     !           ',
			'name': '         ',
			'options': [
				'      ',
				'    ',
				'   !  ',
			],
			'desc': '           .            ',
		},
		{ 
			'rout':      'returning-customer-test2',
			'color':     'blue',
			'title':     '1GB',
			'sub-title': ' ',
			'price':     '99',
			'days':      '30 ',
			'info':      '!           ',
			'name':      '    ',
			'options': [
				'      ',
				'    ',
				'   !  ',
			],
			'desc':      '      .            ',
		},
		{ 
			'rout':      'returning-customer-test3',
			'color':     'blue',
			'title':     '8GB',
			'sub-title': ' ',
			'price':     '189',
			'days':      '30 ',
			'info'     : ' At&t         ',
			'name':      '    ',
			'options': [
				'      ',
				'    ',
				'   !  ',
			],
			'desc'     : '      .            ',
		},
	];

	getSlideData(rout:string){
		return this.sliderItems.find(x => x.rout == rout);
	}

	routPackage(slideData:object){
		this.router.navigate( ['sim-order', slideData['rout'] ] );
	}

	ngOnInit() {

	}

}



